async function readChangesetState(cwd = process.cwd()) {
  let preState = await readPreState(cwd);
  let isInPreMode = preState !== undefined && preState.mode === "pre";
  let changesets = await readChangesets(cwd);
  if (isInPreMode && preState !== undefined) {
    let changesetsToFilter = new Set(preState.changesets);
    changesets = changesets.filter(x => !changesetsToFilter.has(x.id));
  }
  return {
    preState: isInPreMode ? preState : undefined,
    changesets
  };
}

const BumpLevels = {
  dep: 0,
  patch: 1,
  minor: 2,
  major: 3
};
async function getVersionsByDirectory(cwd) {
  let {
    packages
  } = await getPackages(cwd);
  return new Map(packages.map(x => [x.dir, x.packageJson.version]));
}
async function getChangedPackages(cwd, previousVersions) {
  let {
    packages
  } = await getPackages(cwd);
  let changedPackages = new Set();
  for (let pkg of packages) {
    const previousVersion = previousVersions.get(pkg.dir);
    if (previousVersion !== pkg.packageJson.version) {
      changedPackages.add(pkg);
    }
  }
  return [...changedPackages];
}
function getChangelogEntry(changelog, version) {
  let ast = unified().use(remarkParse).parse(changelog);
  let highestLevel = BumpLevels.dep;
  let nodes = ast.children;
  let headingStartInfo;
  let endIndex;
  for (let i = 0; i < nodes.length; i++) {
    let node = nodes[i];
    if (node.type === "heading") {
      let stringified = mdastToString(node);
      let match = stringified.toLowerCase().match(/(major|minor|patch)/);
      if (match !== null) {
        let level = BumpLevels[match[0]];
        highestLevel = Math.max(level, highestLevel);
      }
      if (headingStartInfo === undefined && stringified === version) {
        headingStartInfo = {
          index: i,
          depth: node.depth
        };
        continue;
      }
      if (endIndex === undefined && headingStartInfo !== undefined && headingStartInfo.depth === node.depth) {
        endIndex = i;
        break;
      }
    }
  }
  if (headingStartInfo) {
    ast.children = ast.children.slice(headingStartInfo.index + 1, endIndex);
  }
  return {
    content: unified().use(remarkStringify).stringify(ast),
    highestLevel
  };
}
async function execWithOutput(command, args, options) {
  process.stdout.write(`Running: ${command} ${args.join(" ")}` + os.EOL);
  let childProcess = spawn(command, args, {
    cwd: options.cwd
  });
  childProcess.on("stdout", data => process.stdout.write(data));
  childProcess.on("stderr", data => process.stderr.write(data));
  let result = await childProcess;
  if (!(options !== null && options !== void 0 && options.ignoreReturnCode) && result.code !== 0) {
    throw new Error(`The command "${command} ${args.join(" ")}" failed with code ${result.code}\n${result.stdout.toString("utf8")}\n${result.stderr.toString("utf8")}`);
  }
  return {
    code: result.code,
    stdout: result.stdout.toString("utf8"),
    stderr: result.stderr.toString("utf8")
  };
}
function sortChangelogEntries(a, b) {
  if (a.private === b.private) {
    return b.highestLevel - a.highestLevel;
  }
  if (a.private) {
    return 1;
  }
  return -1;
}

const getCurrentBranch = async cwd => {
  const {
    stdout
  } = await execWithOutput("git", ["rev-parse", "--abbrev-ref", "HEAD"], {
    cwd
  });
  return stdout.trim();
};
const pullBranch = async (branch, cwd) => {
  await execWithOutput("git", ["pull", "origin", branch], {
    cwd
  });
};
const push = async (branch, {
  force,
  includeTags,
  cwd
}) => {
  await execWithOutput("git", ["push", "origin", `HEAD:${branch}`, includeTags && "--tags", force && "--force"].filter(x => !!x), {
    cwd
  });
};
const switchToMaybeExistingBranch = async (branch, cwd) => {
  let {
    stderr
  } = await execWithOutput("git", ["checkout", branch], {
    ignoreReturnCode: true,
    cwd
  });
  let isCreatingBranch = !stderr.toString().includes(`Switched to a new branch '${branch}'`);
  if (isCreatingBranch) {
    await execWithOutput("git", ["checkout", "-b", branch], {
      cwd
    });
  }
};
const reset = async (pathSpec, mode = "hard", cwd) => {
  await execWithOutput("git", ["reset", `--${mode}`, pathSpec], {
    cwd
  });
};
const commitAll = async (message, cwd) => {
  await execWithOutput("git", ["add", "."], {
    cwd
  });
  await execWithOutput("git", ["commit", "-m", message], {
    cwd
  });
};
const checkIfClean = async cwd => {
  const {
    stdout
  } = await execWithOutput("git", ["status", "--porcelain"], {
    cwd
  });
  return !stdout.length;
};

async function runPublish({
  script,
  cwd = process.cwd()
}) {
  let branch = await getCurrentBranch(cwd);
  let [publishCommand, ...publishArgs] = script.split(/\s+/);
  let changesetPublishOutput = await execWithOutput(publishCommand, publishArgs, {
    cwd
  });
  await pullBranch(branch, cwd);
  await push(branch, {
    includeTags: true,
    cwd
  });
  let {
    packages,
    tool
  } = await getPackages(cwd);
  let releasedPackages = [];
  if (tool !== "root") {
    let newTagRegex = /New tag:\s+(@[^/]+\/[^@]+|[^/]+)@([^\s]+)/;
    let packagesByName = new Map(packages.map(x => [x.packageJson.name, x]));
    for (let line of changesetPublishOutput.stdout.split("\n")) {
      let match = line.match(newTagRegex);
      if (match === null) {
        continue;
      }
      let pkgName = match[1];
      let pkg = packagesByName.get(pkgName);
      if (pkg === undefined) {
        throw new Error(`Package "${pkgName}" not found.` + "This is probably a bug in the action, please open an issue");
      }
      releasedPackages.push(pkg);
    }
  } else {
    if (packages.length === 0) {
      throw new Error(`No package found.` + "This is probably a bug in the action, please open an issue");
    }
    let pkg = packages[0];
    let newTagRegex = /New tag:/;
    for (let line of changesetPublishOutput.stdout.split("\n")) {
      let match = line.match(newTagRegex);
      if (match) {
        releasedPackages.push(pkg);
        break;
      }
    }
  }
  if (releasedPackages.length) {
    return {
      published: true,
      publishedPackages: releasedPackages.map(pkg => ({
        name: pkg.packageJson.name,
        version: pkg.packageJson.version
      }))
    };
  }
  return {
    published: false
  };
}
async function runVersion({
  script,
  cwd = process.cwd(),
  commitMessage = "Version Packages"
}) {
  let branch = await getCurrentBranch(cwd);
  let versionBranch = `changeset-release/${branch}`;
  let {
    preState
  } = await readChangesetState(cwd);
  await switchToMaybeExistingBranch(versionBranch, cwd);
  await reset("HEAD", undefined, cwd);
  let versionsByDirectory = await getVersionsByDirectory(cwd);
  if (script) {
    let [versionCommand, ...versionArgs] = script.split(/\s+/);
    await execWithOutput(versionCommand, versionArgs, {
      cwd
    });
  } else {
    let changesetsCliPkgJson = await require(path.join(cwd, "node_modules", "@changesets", "cli", "package.json"));
    let cmd = semverLt(changesetsCliPkgJson.version, "2.0.0") ? "bump" : "version";
    await execWithOutput("node", ["./node_modules/@changesets/cli/bin.js", cmd], {
      cwd
    });
  }
  let changedPackages = await getChangedPackages(cwd, versionsByDirectory);

  // project with `commit: true` setting could have already committed files
  if (!(await checkIfClean(cwd))) {
    const finalCommitMessage = `${commitMessage}${preState ? ` (${preState.tag})` : ""}`;
    await commitAll(finalCommitMessage, cwd);
  }
  await push(versionBranch, {
    force: true,
    cwd
  });
  return {
    versionBranch,
    changedPackages
  };
}

